.field-toolbar {
  width: 300px;
  background: white;
  border-right: 1px solid #ddd;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 4px rgba(0, 0, 0, 0.05);
}

.toolbar-section {
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.toolbar-section:last-child {
  border-bottom: none;
  flex: 1;
  overflow-y: auto;
}

.toolbar-section h3 {
  margin: 0 0 15px 0;
  font-size: 14px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.field-buttons {
  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;
}

.field-btn {
  padding: 10px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
  transition: all 0.2s;
  text-align: left;
}

.field-btn:hover {
  border-color: #2196F3;
  background: #f5f5f5;
  color: #2196F3;
}

.fields-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.empty-message {
  text-align: center;
  color: #999;
  font-size: 12px;
  padding: 20px 0;
}

.field-item {
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
  background: white;
}

.field-item.selected {
  border-color: #2196F3;
  background: #f5f9ff;
}

.field-item-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px;
  cursor: pointer;
  user-select: none;
  transition: background 0.2s;
}

.field-item-header:hover {
  background: #f9f9f9;
}

.field-item.selected .field-item-header {
  background: #e3f2fd;
}

.field-item-type {
  font-size: 16px;
  min-width: 20px;
}

.field-item-label {
  flex: 1;
  font-size: 13px;
  font-weight: 500;
  color: #333;
  text-transform: capitalize;
}

.btn-delete {
  background: #ff5252;
  color: white;
  border: none;
  border-radius: 3px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  transition: background 0.2s;
}

.btn-delete:hover {
  background: #ff1744;
}

.field-editor {
  padding: 12px;
  border-top: 1px solid #eee;
  background: #fafafa;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.editor-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.editor-group label {
  font-size: 12px;
  font-weight: 500;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

.editor-group input[type='text'],
.editor-group input[type='date'],
.editor-group input[type='file'] {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 3px;
  font-size: 12px;
  font-family: inherit;
}

.editor-group input[type='text']:focus,
.editor-group input[type='date']:focus,
.editor-group input[type='file']:focus {
  outline: none;
  border-color: #2196F3;
  box-shadow: 0 0 0 2px rgba(33, 150, 243, 0.1);
}

.editor-group input[type='checkbox'] {
  margin-right: 6px;
}

.value-indicator {
  font-size: 11px;
  color: #4CAF50;
  font-weight: 500;
}

.coords-display {
  display: flex;
  flex-direction: column;
  gap: 4px;
  font-size: 11px;
  color: #666;
  background: white;
  padding: 8px;
  border-radius: 3px;
  border: 1px solid #ddd;
  font-family: monospace;
}

@media (max-width: 768px) {
  .field-toolbar {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #ddd;
    max-height: 200px;
    flex-direction: row;
  }

  .toolbar-section {
    flex: 1;
    overflow-y: auto;
  }

  .field-buttons {
    grid-template-columns: 1fr 1fr;
  }
}
