.pdf-viewer {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #fff;
  overflow: hidden;
  position: relative;
}

.pdf-canvas-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow: auto;
  background: #f9f9f9;
  padding: 20px;
}

.pdf-canvas-container {
  position: relative;
  display: inline-block;
}

.pdf-canvas {
  display: block;
  max-width: 100%;
  height: auto;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #ddd;
}

.fields-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.fields-overlay > * {
  pointer-events: auto;
}

.pdf-controls {
  border-top: 1px solid #ddd;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  background: #f9f9f9;
}

.pdf-controls button {
  padding: 8px 15px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.pdf-controls button:hover:not(:disabled) {
  background: #f0f0f0;
  border-color: #999;
}

.pdf-controls button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pdf-controls span {
  font-size: 14px;
  color: #666;
  font-weight: 500;
}

@media (max-width: 768px) {
  .pdf-canvas-wrapper {
    padding: 10px;
  }

  .pdf-controls {
    padding: 10px;
    gap: 10px;
  }

  .pdf-controls button {
    padding: 6px 12px;
    font-size: 12px;
  }
}
